.works{
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 70px;
        position: absolute;
        cursor: pointer;

        &.left{
            left: 175px; // normally 100px
            transform: rotate(180deg);
        }

        &.right{
            right: 175px; // normally 100px
        }
    }

    .slider{
        height: 550px; // adjusts height of the slider container normally 350px
        display: flex;
        position: absolute;
        left: 0vw;
        transition: all 1s ease-out;

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 1000px; // 700px
                height: 100%; // 100%
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                .left{
                    flex: 4; //normally 4
                    height: 80%; //noramlly 80%
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%; // normally 90%
                        height: 100%; // normally 70%
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer{
                            width: 40px; //40px
                            height: 40px; //40px
                            border-radius: 50%; //normally 50%
                            border-color: rgb(229, 168, 145);
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img{
                                width: 25px;
                            }
                        }
                        h2{
                            font-size: 20px;
                        }
                        p{
                            font-size: 13px;
                        }
                        span{
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }
                .right{
                    flex: 8;
                    height: 100%;
                    border-color: green;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden; // hides corners of the image

                    img{
                        width: 400px;
                        transform: rotate(-10deg);
                    }

                }
            }
        }
    }
}